@font-face {
  font-family: "Flaticon";
  src: url("../fonts/Flaticon.eot");
  src: url("../fonts/Flaticon.woff") format("woff"),
    url("../fonts/Flaticon.ttf") format("truetype"),
    url("../fonts/Flaticon.svg#Flaticon") format("svg");
  font-weight: normal;
  font-style: normal;
}

@media screen and (-webkit-min-device-pixel-ratio: 0) {
  @font-face {
    font-family: "Flaticon";
    src: url("../fonts/Flaticon.svg#Flaticon") format("svg");
  }
}

[class^="flaticon-"]:before,
[class*=" flaticon-"]:before,
[class^="flaticon-"]:after,
[class*=" flaticon-"]:after {
  font-family: Flaticon;
  font-style: normal;
}

.flaticon-point-mark-on-a-circle:before {
  content: "\f100";
}

.flaticon-message:before {
  content: "\f101";
}

.flaticon-briefcase:before {
  content: "\f102";
}

.flaticon-coffee-cup:before {
  content: "\f103";
}

.flaticon-emoticon-square-smiling-face-with-closed-eyes:before {
  content: "\f104";
}

.flaticon-smiley:before {
  content: "\f105";
}

.flaticon-computer:before {
  content: "\f106";
}

.flaticon-people:before {
  content: "\f107";
}

.flaticon-lightbulb-idea:before {
  content: "\f108";
}

.flaticon-gears:before {
  content: "\f109";
}

.flaticon-technology:before {
  content: "\f10a";
}

.flaticon-monitor:before {
  content: "\f10b";
}

body {
  background-color: #ffffff;
  color: #aeaeae;
  font-size: 15px;
  line-height: 1.6;
  font-family: "Poppins", serif;
}

a {
  color: #aeaeae;
  text-decoration: none;
}
a.normalLink {
  color: #337ab7;
}
blockquote,
a h1,
a h2,
a h3,
a h4,
a h5,
a h6,
h1 a,
h2 a,
h3 a,
h4 a,
h5 a,
h6 a,
h1,
h2,
h3,
h4,
h5,
h6,
.h1,
.h2,
.h3,
.h4 {
  color: #000000;
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  line-height: 1.1;
}

.teammembers img,
.blog-box,
.cop-logo img,
.service-box,
a {
  outline: none;
  text-decoration: none !important;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -ms-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}

a:hover,
a:focus {
  color: #000000;
}

.check li:before {
  content: "\f00c";
  font-family: "FontAwesome";
  font-size: 16px;
  left: 0;
  padding-right: 10px;
  position: relative;
  top: 2px;
}

.check li {
  font-size: 13px;
  list-style: none;
  margin-bottom: 8px;
}

.check li:last-child {
  margin-bottom: 0;
}

.check {
  margin-left: 0;
  padding-left: 0;
}

/*********************
MENU
*********************/

.vertical-header {
  position: fixed;
  left: 0;
  z-index: 1111;
  top: 0;
  bottom: 0;
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.vertical-header-wrapper {
  width: 100%;
  height: 100%;
  /* background: rgba(0, 0, 0, 1) url("images/menubg.jpg") no-repeat scroll center center; */
  z-index: 10;
  overflow-x: hidden;
  min-width: 260px;
  overflow-y: hidden;
  opacity: 0.8;
  background: #000;
}

.subsub-menu-wrapper,
.sub-menu-wrapper {
  opacity: 0;
  visibility: hidden;
  background-color: #151515;
  position: absolute;
  padding: 0px 10px 0;
  width: 100%;
  top: 0;
  max-width: 240px;
  bottom: 0;
  left: 60%;
  height: 100%;
  display: table;
  -webkit-transition: visibility 0.35s ease-in-out, opacity 0.35s ease-in-out,
    top 0.35s ease-in-out, bottom 0.35s ease-in-out, left 0.35s ease-in-out,
    right 0.35s ease-in-out;
  -moz-transition: visibility 0.35s ease-in-out, opacity 0.35s ease-in-out,
    top 0.35s ease-in-out, bottom 0.35s ease-in-out, left 0.35s ease-in-out,
    right 0.35s ease-in-out;
  -o-transition: visibility 0.35s ease-in-out, opacity 0.35s ease-in-out,
    top 0.35s ease-in-out, bottom 0.35s ease-in-out, left 0.35s ease-in-out,
    right 0.35s ease-in-out;
  transition: visibility 0.35s ease-in-out, opacity 0.35s ease-in-out,
    top 0.35s ease-in-out, bottom 0.35s ease-in-out, left 0.35s ease-in-out,
    right 0.35s ease-in-out;
}

.subsub-menu-wrapper {
  background-color: #191919;
}
.primary-menu {
  height: auto;
  max-height: 60vh;
  overflow: auto;
  margin-top: 2rem;
}
.primary-menu li:hover .subsub-menu li:hover a,
.primary-menu li:hover .sub-menu-wrapper li:hover a,
.primary-menu li:hover a {
  color: rgba(255, 255, 255, 1);
}

.primary-menu li:hover .subsub-menu li a,
.primary-menu li:hover .sub-menu-wrapper li a,
.menu-search .btn,
.vertical-header li a {
  color: rgba(255, 255, 255, 0.7);
}

.primary-menu li:hover .subsub-menu li a {
  color: rgba(255, 255, 255, 0.7) !important;
}

.primary-menu li:hover .subsub-menu li:hover a {
  color: rgba(255, 255, 255, 1) !important;
}

.vertical-header li {
  list-style: none;
}

.vertical-header ul {
  padding: 1rem 0;
}
@media only screen and (min-width: 768px) and (max-width: 991px) {
  .vertical-header ul {
    padding: 3rem 0;
  }
}
.menuHeading {
  color: #c2d82e;
}
.last-child-menu {
}
.sub-menu li:hover .subsub-menu-wrapper,
:hover .sub-menu-wrapper {
  opacity: 1;
  z-index: 1000;
  left: 100%;
  position: absolute;
  visibility: visible;
}

.primary-menu li a {
  display: block;
  width: 100%;
  position: relative;
  text-decoration: none !important;
}

.primary-menu li a:hover {
  color: #c2d82e;
  font-weight: bold;
}

.primary-menu li .fa-angle-right {
  position: absolute;
  right: 0;
  font-size: 14px;
  top: 0px;
}

.yamm .fa-shopping-bag,
.navbar-inverse .fa-shopping-bag,
.primary-menu .fa-shopping-bag {
  margin-left: 4px;
}

.primary-menu {
  list-style: none;
}

@media (max-width: 768px) {
  .primary-menu {
    list-style: none;
    margin-top: -10px;
    margin-left: 2%;
