.menu-search {
  padding: 0 15px;
}

.menu-search .form-group {
  position: relative;
  margin: 0;
  padding: 0;
}

.menu-search .btn {
  background-color: transparent !important;
  position: absolute;
  border: 0;
  top: -1px;
  padding-right: 10px;
  padding-left: 10px;
  font-size: 12px;
  right: 0;
}

.menu-search .form-control:focus .btn {
