}

.menu-social ul {
  margin: 0;
  padding: 0;
  background: transparent;
}

.menu-social ul li {
  margin-top: 0;
  padding-top: 0;
}

.menu-social ul li i {
  font-size: 12px;
  padding: 12px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 100%;
  width: 40px;
  height: 40px;
}
.menu-social ul li:hover i {
