/*********************
SECTIONS
*********************/

.overlay {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  /* z-index: 1; */
  background: url(../images/pattern.png) repeat center center rgba(0, 0, 0, 0.7);
}

.video-section {
  position: relative;
  width: 100%;
  height: 100vh;
  @media (max-width: 767px) {
    .img-responsive {
      object-fit: cover;
      height: 100%;
    }
  }
}


.container {
  width: 100%;
  max-width: 1180px;
}

.section {
  padding: 10px 0px;
  position: relative;
  overflow: hidden;
  display: block;
  background-color: #000 !important;
  opacity: 0.8;
}

.section.noover {
  overflow: visible !important;
}

.section-title {
  // margin-top: 45px;
  /* margin-bottom: 45px; */
}

.text-widget h3,
.section-title h3 {
  margin: 0 0 25px;
  padding: 0;
  font-size: 28px;
  font-weight: 600;
}

.section-title p {
  max-width: 800px;
  margin: 0 auto;
  padding: 0;
}

.section.lb {
  background-color: #f7f6f6;
}

.section.db {
  background-color: #000000;
}

.nopadtop {
  padding-top: 0;
}

.blog-box:hover,
.service-box:hover,
.shadow {
  box-shadow: 0px 7px 14px rgba(0, 0, 0, 0.1);
}

.parallax {
  background-attachment: fixed;
  background-position: 50% 0;
  background-repeat: no-repeat;
  height: 100%;
  position: relative;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}

.parallax.parallax-off {
  background-attachment: scroll;
  display: block;
  height: 100%;
  min-height: 100%;
  overflow: hidden;
  position: relative;
  background-position: top center;
  vertical-align: sub;
  width: 100%;
  z-index: 2;
}

.no-scroll-xy {
  overflow: hidden !important;
  -webkit-transition: all 0.4s ease-in-out;
  -moz-transition: all 0.4s ease-in-out;
  -ms-transition: all 0.4s ease-in-out;
  -o-transition: all 0.4s ease-in-out;
  transition: all 0.4s ease-in-out;
}

